import React, { Component } from 'react';
import { Link, Route } from 'react-router-dom';
import AnimatedSwitch from '../components/animated-switch';
import axios from 'axios';

import MemberForm from '../components/MemberForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';
import confirmAction from '../utils/confirmAction';

export default class MemberPages extends Component {
    render() {
        let match = this.props.match;
        return <AnimatedSwitch>
            <Route path={`${match.path}/new`} render={(props) => <MemberNew {...props} /> } />
            <Route path={`${match.path}/:memberId`} render={(props) => <MemberEdit {...props} /> } />
            <Route exact path={`${match.path}`} render={(props) => <Member {...props} /> } />
        </AnimatedSwitch>
    }
}

export class Member extends Component {
    constructor(props) {
        super(props);
        window.memberListActionEdit = this.edit;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                axios.get('/api/members', {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'firstname', name: 'firstname', 'class': 'text-center'},
                { data: 'lastname', name: 'lastname', 'class': 'text-center'},
                { data: 'title', name: 'title', 'class': 'text-center'},
                { data: 'phone', name: 'phone', 'class': 'text-center'},
                { data: 'email', name: 'email', 'class': 'text-center'},
                { data: 'id_no', name: 'id_no', 'class': 'text-center'},
                { data: 'doj', name: 'doj', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="memberListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                    </span>`;
                }}
            ],
            createdRow: function (row, data, index) {
                if(data.date_left) {
                    row.classList.add('table-danger');
                }
            }
        };
    }

    edit = (member) => {
        this.props.history.push(`${this.props.match.url}/${member}`);
    }

	render() {
		let match = this.props.match;
		return <div className="container-fluid">
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <Link to={`${match.url}/new`} className="btn btn-success details" href="/school/students/create">New Member</Link>
        	      </span>
        	    </div>
    	    </div>
            <DataTable ref={this.dataTable} title="Members" config={this.dataTableConfig}>
                <th>Firstname</th>
                <th>Lastname</th>
                <th>Title</th>
                <th>Phonenumber</th>
                <th>Email</th>
                <th>ID Number</th>
                <th>Date Joined</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

export class MemberNew extends Component {
    state = { error: {} };
    submit = (data) => {
        const formData = new FormData();
        formData.append('_method', 'POST');
        for(let i in data){
            formData.append(i, data[i]);
        }

        return axios.post('/api/members', formData).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/members');
    }
    render() {
        return <MemberForm error={this.state.error} submit={this.submit} close={this.close} />
    }
}

export class MemberEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
    if(this.props.location.state && this.props.location.state.member){
        this.setState({data: this.props.location.state.member})
    }else if(this.props.match.params.memberId){
        axios.get(`/api/members/${this.props.match.params.memberId}`).then(response => {
            this.setState({data: response.data});
        },error=>this.setState({error:error.response.data}))
    }
    
  }

  submit = (data) => {
        const formData = new FormData();
        formData.append('_method', 'PUT');
        for(let i in data){
            formData.append(i, data[i]);
        }
        return axios.post(`/api/members/${this.props.match.params.memberId}`, formData, { headers: { 'content-type': 'multipart/form-data' },processData: false, contentType: false }).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/members');
    }

    render() {
        return <MemberForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
    }
}