import React, { Component } from 'react';
import lodash from 'lodash';

import ShowNotification from '../components/ShowNotification';
import DateInput from '../components/DateInput';
import confirmAction from '../utils/confirmAction';

export default class MemberForm extends Component {
	defaults = {
		firstname: '',
		lastname: '',
		title: '',
		id_no: '',
		gender: '',
		phone: '',
		email: '',
		doj: ''
	};

	state = {previewUrl: '', error: {}, loading: false, data: {...this.defaults}
	};

	componentDidUpdate(prevProps,prevState) {
		if(this.props.edit){
			if(!lodash.isEqual(this.props.data, prevProps.data)){
				this.setState({data: {...this.state.data,...this.props.data}})
			}
			if(this.props.data.profile != prevProps.data.profile){
				let url = '/uploads/members_profile/' + this.props.data.profile;
				this.setState({previewUrl: url});
			}
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (field,event) => {
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}

	profileChanged = (event) => {
		let files = event.target.files;
		if(files && files[0]) {
			var reader = new FileReader();
			reader.onload = (e) => {
				this.setState({previewUrl: e.target.result});
			};
			reader.readAsDataURL(files[0]);
			this.setState( (state) => state.data['profile'] = files[0])
		}
	}

	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Member Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Member Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error: error.response.data});
			return Promise.reject(error.response.data);
		}).finally(_=>this.setState({loading:false}));
	}

	retire = () => {
		confirmAction( () => {
			return axios.post(`/api/members/retire/${this.props.data.id}`).then(res=>{
				this.props.close();
				return {success: 'Retired!', succes_text: 'Member was retired'};
			}, error => {
				throw {error: "Member couldn't be retired"};
			})
		}, {loading_title: 'Retiring...', loading_text: 'Retiring Member', canceled: "Retiring Member Operation Cancelled", consequence: 'Member information will be archieved'});
	}

	restore = () => {
		confirmAction( () => {
      return axios.post(`/api/members/restore/${this.props.data.id}`).then(res=>{
        this.props.close();
        return {success: 'Restored!', succes_text: 'Member was restored'};
      }, error => {
        throw {error: 'Member could not be restored.'};
      })
    }, {loading_title: 'Restoring...', loading_text: 'Restoring Member', canceled: "Restoring Member Operation Cancelled", consequence: 'Member information will be made accessible again'});
	}

	delete = () => {
		confirmAction( () => {
			return axios.delete(`/api/members/${this.props.data.id}`).then(res=>{
				this.props.close();
				return {success: 'Deleted!', succes_text: 'Member was deleted'};
			}, error => {
				throw {error: "Member couldn't be deleted."};
			})
		}, {loading_title: 'Deleting...', loading_text: 'Deleting Member', canceled: "Deleting Member Operation Cancelled", consequence: 'Member will be completely removed'});
	}

	render() {
		let error = this.state.error || {};
		let firstnameInvalid='', lastnameInvalid='', titleInvalid='', genderInvalid='', emailInvalid='', idNoInvalid='', phoneInvalid='', dojInvalid='';
		if(error.errors) {
			firstnameInvalid = error.errors.firstname ? 'is-invalid' : '';
			lastnameInvalid = error.errors.lastname ? 'is-invalid' : '';
			titleInvalid = error.errors.title ? 'is-invalid' : '';
			genderInvalid = error.errors.gender ? 'is-invalid' : '';
			emailInvalid = error.errors.email ? 'is-invalid' : '';
			idNoInvalid = error.errors.id_no ? 'is-invalid' : '';
			phoneInvalid = error.errors.phone ? 'is-invalid' : '';
			dojInvalid = error.errors.doj ? 'is-invalid' : '';
		}

		return <div className="container-fluid" >
			{this.props.edit && <div className="row">
    	    <div className="col-md-12">
    	      <span className="pull-right">
    	      	{this.state.data.date_left ? <button type="button" className="btn btn-secondary" onClick={this.restore} >Restore</button>
    	      	 : <button type="button" className="btn btn-warning" onClick={this.retire} >Retire</button> }
    	      	&nbsp;
    	        <button type="button" className="btn btn-danger" onClick={this.delete} >Delete</button>
    	      </span>
    	    </div>
	    </div>}
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Member Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					    	<div className="row">
					    		<div className="col-md-9">
					    				<div className="form-group">
								        <label>First Name</label>
								        <input value={this.state.data.firstname} onChange={this.inputChanged.bind(this,'firstname')} type="text" placeholder="First Name" className={`form-control ${firstnameInvalid}`} />
								        {firstnameInvalid && error.errors.firstname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>

								      <div className="form-group">
								        <label>Last Name</label>
								        <input value={this.state.data.lastname} onChange={this.inputChanged.bind(this,'lastname')} type="text" placeholder="Last Name" className={`form-control ${lastnameInvalid}`} />
								        {lastnameInvalid && error.errors.lastname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>

								      <div className="form-group">
								        <label>Title</label>
								        <input value={this.state.data.title} onChange={this.inputChanged.bind(this,'title')} type="text" placeholder="Title" className={`form-control ${titleInvalid}`} />
								        {titleInvalid && error.errors.title.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>
					    		</div>
						      <div className="col-md-3 d-flex align-items-center">
						      	<div>
						      	
						      	<div className="input-group">
						      		<div>
							      		<img src={this.state.previewUrl || '/img/profile-placeholder.jpg'} className="rounded img-fluid" style={{width: '100%', height: '100%'}} alt="Responsive image" />
							      	</div>
										  <span style={{position: 'absolute', bottom: 0}}>
										  	<span className="btn btn-primary"><i className="fa fa-camera"></i>Change</span>
										    <input type="file" onChange={this.profileChanged} style={{position: 'absolute', bottom: 0, top: 0, right: 0, left: 0}} className="custom-file-input" id="inputGroupFile01" aria-describedby="inputGroupFileAddon01" />
										  </span>
										</div>
										</div>
						      </div>
					      </div>

					      <div className="row">
					      	<div className="form-group col-md-6">
						        <label>ID Number</label>
						        <input value={this.state.data.id_no} onChange={this.inputChanged.bind(this,'id_no')} type="text" placeholder="ID Number" className={`form-control ${idNoInvalid}`} />
						        {idNoInvalid && error.errors.id_no.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-6">
						        <label>Gender</label>
						        <select value={this.state.data.gender} onChange={this.inputChanged.bind(this,'gender')} name="gender" className={`form-control ${genderInvalid}`} >
						        	<option value="">Select Gender</option>
                      <option value="M">Male</option>
                      <option value="F">Female</option>
                    </select>
						        {genderInvalid && error.errors.gender.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

						    <div className="row">
						      <div className="form-group col-md-6">
						        <label>Phone Number</label>
						        <input value={this.state.data.phone} onChange={this.inputChanged.bind(this,'phone')} type="text" placeholder="Phone Number" className={`form-control ${phoneInvalid}`} />
						        {phoneInvalid && error.errors.phone.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-6">
						        <label>Email</label>
						        <input value={this.state.data.email} onChange={this.inputChanged.bind(this,'email')} type="text" placeholder="Email" className={`form-control ${emailInvalid}`} />
						        {emailInvalid && error.errors.email.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>



					      <div className="form-group col-md-6">
					        <label>Date Joined</label>
					        <DateInput value={this.state.data.doj} onChange={this.inputChanged.bind(this,'doj')} type="text" placeholder="Date Joined" className={`form-control ${dojInvalid}`} />
					        {dojInvalid && error.errors.doj.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
						  	{this.props.edit ? <div className="col-sm-2">
			            	<input onClick={this.apply} type="button" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
			          	</div> :
			          	<div className="col-sm-3">
			            	<input onClick={this.saveContinue} type="button" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
			          	</div>
			          }
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}