import React, {Component} from 'react';

import lodash from 'lodash';

import 'datatables.net-bs4';
import 'datatables.net-buttons-bs4';
import 'datatables.net-buttons/js/buttons.html5.js';
import 'datatables.net-colreorder-bs4';
import 'datatables.net-fixedcolumns-bs4';
import 'datatables.net-fixedheader-bs4';
import 'datatables.net-responsive-bs4';
import 'datatables.net-rowgroup-bs4';
import 'datatables.net-rowreorder-bs4';
import 'datatables.net-scroller-bs4';
import 'datatables.net-select-bs4';
import '../libs/dataTables.cellEdit';

//datatables dependencies
// import jsZip from  'jszip';
// import 'pdfmake';
// import pdfFonts from 'pdfmake/build/vfs_fonts.js';

// pdfMake.vfs = pdfFonts.pdfMake.vfs;
// window.JSZip = jsZip;

export default class DataTable extends Component {
  tableEl = React.createRef();
  state = {}
  table = null;
  constructor(props) {
    super(props);
    const self = this;
    this.defaultConfig = {
      "pagingType": "full_numbers",
      "lengthMenu": [
        [10, 25, 50, 100, -1],
        [10, 25, 50, 100, "All"]
      ],
      dom: 'rt<"bottom"lpi><"clear">',
      buttons: [
        {
          extend:    'excelHtml5',
          text:      '<i class="fa fa-file-excel-o text-info"></i>',
          titleAttr: 'Excel',
          exportOptions: {
            columns: "thead th:not(.noExport)"
          }
        }
      ],
      responsive: true,
      language: {
        search: "_INPUT_",
        searchPlaceholder: "Search records",
      },
      rowCallback: function( row, data ) {
        let jRow = $(row);
        jRow.tooltip('dispose');
        let title = '';
        if(data.__meta_pending) {
            title = 'Pending - ';
            let className = 'table-dark';
            if(data.__meta_error && data.__meta_error.message) {
                className = (data.__meta_error.message !== "Network Error") ? 'table-warning' : className;
                let error = data.__meta_error;
                title += error.message;
                if(error.errors) {
                    for(let errorSub of Object.keys(error.errors) ){
                      title += '\n' + error.errors[errorSub];
                    }
                }
            }
            row.classList.add(className);
        } else if(data[self.props.softDeleteField]) {
            title = 'Discontinued';
            row.classList.add('table-danger');
        } else {
          row.classList.remove('table-warning');
          row.classList.remove('table-dark');
          row.classList.remove('table-danger');
        }

        if(title)
            jRow.tooltip({toggle: "tooltip", placement: "bottom", title });
      }
    };
  }
  
  componentDidMount() {
    this.initTable();
  }

  componentWillUnmount() {
      $(this.tableEl.current).find('tr[title]').tooltip('dispose');
  }

  initTable() {
    const self = this;
    let config = this.props.config || {};
    let configRowCallback = this.props.config.rowCallback;
    config.rowCallback = (row, data) => {
      self.highlightRow(row, data);
      configRowCallback && configRowCallback(row, data);
    };
    this.table = $(this.tableEl.current).DataTable({...this.defaultConfig, ...config});
  }

  componentDidUpdate(prevProp,prevState) {
    if(!lodash.isEqual(prevProp.config, this.props.config)) {
      this.table.destroy();
      this.initTable();
    }
  }

  draw = () => {
    this.table.draw();
  }

  highlightRow = (row, data) => {
    let jRow = $(row);
      jRow.tooltip('dispose');
      let title = '';
      if(data.__meta_pending) {
          title = 'Pending - ';
          let className = 'table-dark';
          if(data.__meta_error && data.__meta_error.message) {
              className = (data.__meta_error.message !== "Network Error") ? 'table-warning' : className;
              let error = data.__meta_error;
              title += error.message;
              if(error.errors) {
                  for(let errorSub of Object.keys(error.errors) ){
                    title += '\n' + error.errors[errorSub];
                  }
              }
          }
          row.classList.add(className);
      } else if(data[this.props.softDeleteField]) {
          title = 'Discontinued';
          row.classList.add('table-danger');
      } else {
        row.classList.remove('table-warning');
        row.classList.remove('table-dark');
        row.classList.remove('table-danger');
      }

      if(title)
          jRow.tooltip({toggle: "tooltip", placement: "bottom", title });
  }

  render() {
    return <div className="row">
      <div className="col-md-12">
        <div className="card data-tables">
        <div className="card-header text-center">
          <h3>{this.props.title}</h3>
        </div>
        <div className="card-body table-striped table-no-bordered table-hover dataTable dtr-inline table-full-width">
          <div className="toolbar col-12">
            <div className="row">
              {this.props.toolbar}
            </div>
          </div>
          <div className="fresh-datatables">
          <table ref={this.tableEl} className="table table-striped table-no-bordered table-hover" cellSpacing="0" width="100%" style={{width: '100%'}}>
            <thead>
              <tr>
                {this.props.children}
              </tr>
            </thead>
            <tfoot>
              <tr>
                {this.props.children}
              </tr>
            </tfoot>
          </table>
          </div>
        </div>
        </div>
      </div>
    </div>
  }
}
