import Swal from 'sweetalert2';

export default function(action, config={} ) {
	return Swal.fire({
	  title: 'Are you sure?',
	  text: config.consequence || "You won't be able to revert this!",
	  icon: 'warning',
	  showCancelButton: true,
	  confirmButtonColor: '#3085d6',
	  cancelButtonColor: '#d33',
	  confirmButtonText:  config.yes || 'Yes'
	}).then((result) => {
	  if (result.value) {
	  	let loadingModal = Swal.fire({
	      title: config.loading || 'Waiting...',
	      text: config.loading_text || 'waiting for operation to complete',
	      showConfirmButton: false,
	      onBeforeOpen: () => {
	   	 		Swal.showLoading()
	   	 	}
	    });

	    action().then(res=>{
	  		loadingModal.closeModal();
	  		Swal.fire(
		      res.success,
		      res.succes_text,
		      'success'
		    );
			}, error => {
				loadingModal.closeModal();
				Swal.fire({
				  icon: 'error',
				  title: 'Oops...',
				  text: error.error,
				  footer: error.footer || ''
				});
			});
	  }
	  else if (result.dismiss === Swal.DismissReason.cancel) {
	    Swal.fire(
	      'Cancelled',
	      config.canceled,
	      'error'
	    )
	  }
	});
}